import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { axiosInstance, BASE_URL } from "./axiosInstance";
import Socialmediapage from "./socialmediapage";
import Spinner from '../components/Spinneroverlay' // Correct import

function CombinedDashboard() {
  const navigate = useNavigate();
  const location = useLocation();

  const [authToken, setAuthToken] = useState(localStorage.getItem("auth_token"));
  const [user, setUser] = useState(null);
  // const [publicData, setPublicData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loginPrompt, setLoginPrompt] = useState(false);
  const [loginMessage, setLoginMessage] = useState(null);

  // Handle redirect from social login (e.g., Facebook/Instagram)
  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const token = params.get("token");
    const encodedUser = params.get("user");

    if (token && encodedUser) {
      localStorage.setItem("auth_token", token);
      localStorage.setItem("user", decodeURIComponent(encodedUser));
      setAuthToken(token);
      setLoginMessage("Login successful.");
      navigate("/gallary", { replace: true }); // Clean URL
    }
  }, [location, navigate]);

  useEffect(() => {
    const fetchPublicPosts = async () => {
      try {
        const res = await axiosInstance.get("/public-posts");
        // setPublicData(res.data);
      } catch (err) {
        console.error("Failed to fetch public posts", err);
      } finally {
        setLoading(false);
      }
    };

    fetchPublicPosts();
  }, []);

  useEffect(() => {
    const verifyToken = async () => {
      if (!authToken) return;
      try {
        const res = await axiosInstance.get("/user");
        setUser(res.data.user);
      } catch (err) {
        console.error("Token invalid/expired", err);
        logout();
      }
    };

    verifyToken();
  }, [authToken]);

  const handleEditRequest = () => {
    if (!authToken) {
      setLoginPrompt(true);
    } else {
      alert("Edit mode enabled!");
    }
  };

  const loginWith = (provider) => {
    localStorage.removeItem("auth_token");
    localStorage.removeItem("user");
    window.location.href = `${BASE_URL}/api/auth/${provider}/redirect`;
    console.log({provider});
  };

  const logout = () => {
    localStorage.clear();
    setAuthToken(null);
    setUser(null);
  };

  // if (loading) return <p className="p-4">Loading... </p>;
 

  return (
    <div className="container mx-auto px-4 py-6">
      {/* <h1 className="text-3xl font-bold mb-6">Welcome to the Site</h1> */}

      {/* {loginMessage && (
        <p className="mb-4 text-green-600 font-semibold">{loginMessage}</p>
      )} */}

     
      {/* {publicData.map((item, index) => (
        <div key={index} className="bg-gray-100 p-4 rounded mb-3 shadow">
          <p>{item.title}</p>
        </div>
      ))} */}

      {/* Edit / Upload */}
      {/* <div className="mt-6">
        <button
          onClick={handleEditRequest}
          className="px-4 py-2 bg-yellow-500 text-white rounded"
        >
          Edit / Upload
        </button>
      </div> */}

      {/* Login prompt */}
      {/* {loginPrompt && !authToken && (
        <div className="mt-6 p-4 bg-white rounded shadow">
          <p className="mb-2 font-semibold text-red-600">
            Login required to continue
          </p>
          <button
            onClick={() => loginWith("facebook")}
            className="px-4 py-2 mr-2 bg-blue-600 text-white rounded"
          >
            Login with Facebook
          </button>
          <button
            onClick={() => loginWith("instagram")}
            className="px-4 py-2 bg-pink-500 text-white rounded"
          >
            Login with Instagram
          </button>
        </div>
      )} */}

      {/* Logged-in user info */}
      {/* {user && (
        <div className="mt-6 p-4 bg-green-100 rounded">
          <img
            src={user.avatar}
            alt="User Avatar"
            className="w-10 h-10 rounded-full border border-green-400"
          />
          <p className="font-medium text-green-900">
            Logged in as: {user.name} ({user.role})
          </p>
          <button
            onClick={logout}
            className="mt-2 px-3 py-1 bg-red-500 text-white rounded"
          >
            Logout
          </button>
        </div>
      )} */}
   

    
        {/* <Socialmediapage user={user} logout={logout} authToken={authToken} loginWith={loginWith} loading={loading} /> */}
       <Spinner isLoading={loading} />
 
        <Socialmediapage user={user} logout={logout} authToken={authToken} loginWith={loginWith} loading={loading} />
  
        </div>
  );
}

export default CombinedDashboard;
