import React from 'react';
import './Spinner.css'; // Import your CSS file for styling

// Spinner component
const Spinner = ({ isLoading }) => {
    return (
        <div className={`spinner-overlay ${isLoading ? 'visible' : 'hidden'}`}>
            <div className="spinner-container">
                {/* Apply Tailwind classes directly here if you want to use them */}
                <div className="spinner h-12 w-12 text-blue-500"></div>
            </div>
        </div>
    );
}

export default Spinner;