import React, { useState } from 'react';
// Import the configured axios instance instead of the default axios
import { axiosInstance } from './axiosInstance.jsx'; // Adjust the import path as needed

// --- Component Styles (CSS-in-JS) ---
const styles = {
  container: {
    marginTop: '2rem',
    padding: '2rem',
    backgroundColor: '#f8f9fa',
    borderRadius: '8px',
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    maxWidth: '600px',
    marginLeft: 'auto',
    marginRight: 'auto',
  },
  header: {
    backgroundColor: '#007bff',
    color: 'black', // Changed from white
    padding: '1rem',
    borderRadius: '8px 8px 0 0',
    marginBottom: '1.5rem',
  },
  formGroup: {
    marginBottom: '1.5rem',
  },
  label: {
    fontWeight: 'bold',
    marginBottom: '0.5rem',
    display: 'block',
    color: 'black', // Explicitly set to black
  },
  input: {
    width: '100%',
    padding: '0.75rem',
    border: '1px solid #ced4da',
    borderRadius: '4px',
    fontSize: '1rem',
    color: 'black', // Explicitly set to black
  },
  textarea: {
    width: '100%',
    padding: '0.75rem',
    border: '1px solid #ced4da',
    borderRadius: '4px',
    fontSize: '1rem',
    minHeight: '80px',
    color: 'black', // Explicitly set to black
  },
  button: {
    width: '100%',
    padding: '0.75rem',
    backgroundColor: '#28a745',
    color: 'black', // Changed from white
    border: 'none',
    borderRadius: '4px',
    fontSize: '1.2rem',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  buttonDisabled: {
    backgroundColor: '#6c757d',
    cursor: 'not-allowed',
  },
  spinner: {
    border: '4px solid #ccc', // Changed to light grey
    borderTop: '4px solid #333', // Changed to dark grey
    borderRadius: '50%',
    width: '24px',
    height: '24px',
    animation: 'spin 1s linear infinite',
    marginRight: '10px',
  },
  responseMessage: {
    marginTop: '1.5rem',
    padding: '1rem',
    borderRadius: '4px',
    textAlign: 'center',
  },
  responseSuccess: {
    backgroundColor: '#d4edda',
    color: 'black', // Changed from green
  },
  responseError: {
    backgroundColor: '#f8d7da',
    color: 'black', // Changed from red
  },
};

// --- Keyframe animation for the spinner ---
const keyframes = `
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
`;

const PostCreationForm = () => {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [image, setImage] = useState(null);
  const [otp, setOtp] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage('');
    setError('');

    const formData = new FormData();
    formData.append('title', title);
    formData.append('description', description);
    formData.append('image', image);
    formData.append('otp', otp);

    try {
      // Use the imported axiosInstance for the API call.
      // The base URL is already configured in the instance.
      const response = await axiosInstance.post(
        '/verify-and-upload', // Only the specific endpoint is needed
        formData, {
          headers: {
            // The Authorization header is added automatically by the interceptor.
            'Content-Type': 'multipart/form-data',
          },
        }
      );

      setMessage(response.data.message || 'Post created successfully!');
      setTitle('');
      setDescription('');
      setImage(null);
      setOtp('');
      e.target.reset(); // Reset the file input
    } catch (err) {
      setError(
        err.response?.data?.message || 'An error occurred. Please try again.'
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <style>{keyframes}</style>
      <div style={styles.container}>
        <div style={styles.header}>
          <h4 style={{ margin: 0, color: 'black' }}>Create a New Post</h4>
        </div>
        <form onSubmit={handleSubmit}>
          <div style={styles.formGroup}>
            <label htmlFor="title" style={styles.label}>
              Title
            </label>
            <input
              type="text"
              id="title"
              style={styles.input}
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label htmlFor="description" style={styles.label}>
              Description
            </label>
            <textarea
              id="description"
              style={styles.textarea}
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label htmlFor="image" style={styles.label}>
              Upload Image
            </label>
            <input
              type="file"
              id="image"
              style={styles.input}
              accept="image/*"
              onChange={(e) => setImage(e.target.files[0])}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label htmlFor="otp" style={styles.label}>
              4-Digit OTP
            </label>
            <input
              type="text"
              id="otp"
              style={styles.input}
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
              maxLength="4"
              required
            />
          </div>
          <button
            type="submit"
            style={
              loading ?
              { ...styles.button, ...styles.buttonDisabled } :
              styles.button
            }
            disabled={loading}
          >
            {loading && <div style={styles.spinner}></div>}
            {loading ? 'Uploading...' : 'Submit Post'}
          </button>
        </form>
        {message && (
          <div style={{ ...styles.responseMessage, ...styles.responseSuccess }}>
            {message}
          </div>
        )}
        {error && (
          <div style={{ ...styles.responseMessage, ...styles.responseError }}>
            {error}
          </div>
        )}
      </div>
    </>
  );
};

export default PostCreationForm;