import React, { useState, useEffect, useRef, useCallback } from 'react';
import './socialpage.css';
import FacebookIcon from '../assets/facebook-icon.png';
import GoogleIcon from '../assets/google-icon.png';
import InstagramIcon from '../assets/instagram-icon.png';
import SelectImageIcon from '../assets/select-icon.png';
import UploadImageIcon from '../assets/upload-icon.png';
import UpArrowIcon from '../assets/up-arrow-icon.png';
import DeleteBtn from '../assets/delete.png';
import DefaultAvatarIcon from '../assets/default-avatar.png';
import { axiosInstance } from "./axiosInstance";
import logo from '../assets/kavumainpageimg/logo-kavu.png';

import { LazyLoadImage } from 'react-lazy-load-image-component';

const SocialMediaFeed = ({ user, logout, authToken, loginWith, loading }) => {
    // Add body class when component mounts
    useEffect(() => {
        document.body.classList.add('social-feed-page');
        return () => {
            document.body.classList.remove('social-feed-page');
        };
    }, []);

    const contentUploadDescriptionRef = useRef(null);
    const photoGridRef = useRef(null);
    const itemDetailModalRef = useRef(null);
    const userProfileModalRef = useRef(null);
    const userLoginModalRef = useRef(null);
    const appSettingsDropdownRef = useRef(null);
    const deleteConfirmationModalRef = useRef(null);
    const backToTopBtnRef = useRef(null);

    const [contentTitle, setContentTitle] = useState('');
    const [contentDescription, setContentDescription] = useState('');
    const [imageFile, setImageFile] = useState(null);
    const [isItemDetailModalVisible, setIsItemDetailModalVisible] = useState(false);
    const [activeModalItem, setActiveModalItem] = useState(null);
    const [isUserProfileModalVisible, setIsUserProfileModalVisible] = useState(false);
    const [isUserLoginModalVisible, setIsUserLoginModalVisible] = useState(false);
    const [isAppSettingsDropdownVisible, setIsAppSettingsDropdownVisible] = useState(false);
    const [isDeleteConfirmationModalVisible, setIsDeleteConfirmationModalVisible] = useState(false);
    const [itemToDeleteId, setItemToDeleteId] = useState(null);

    const [items, setItems] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [canLoadMore, setCanLoadMore] = useState(true);
    const [areItemsLoading, setAreItemsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState(null);

    

    const readMoreLimit = 90;

    // Fetch items from Laravel API
    const retrieveItems = useCallback(async (pageToFetch = 1, shouldReset = false) => {
        setAreItemsLoading(true);
        setErrorMessage(null);

        try {
            const response = await axiosInstance.get(`/posts?page=${pageToFetch}`);
            const newItems = response.data.data;

            if (shouldReset) {
                setItems(newItems);
            } else {
                setItems(prevItems => [...prevItems, ...newItems]);
            }

            // Check if there are more pages
            if (newItems.length === 0 || pageToFetch >= response.data.last_page) {
                setCanLoadMore(false);
            }
        } catch (err) {
            setErrorMessage(err.message);
            console.error('Error fetching items:', err);
        } finally {
            setAreItemsLoading(false);
        }
    }, []);

    // Initial load
    useEffect(() => {
        retrieveItems(1, true);
    }, [retrieveItems]);

    // Infinite scroll handler
    useEffect(() => {
        const handlePageScroll = () => {
            if (
                window.innerHeight + document.documentElement.scrollTop <
                document.documentElement.offsetHeight - 500 ||
                areItemsLoading ||
                !canLoadMore
            ) {
                return;
            }

            setCurrentPage(prev => prev + 1);
        };

        window.addEventListener('scroll', handlePageScroll);
        return () => window.removeEventListener('scroll', handlePageScroll);
    }, [areItemsLoading, canLoadMore]);

    // Fetch more items when page changes
    useEffect(() => {
        if (currentPage > 1) {
            retrieveItems(currentPage);
        }
    }, [currentPage, retrieveItems]);

    // Auto-resizing textarea
    useEffect(() => {
        if (contentUploadDescriptionRef.current) {
            contentUploadDescriptionRef.current.style.height = 'auto';
            contentUploadDescriptionRef.current.style.height = Math.max(contentUploadDescriptionRef.current.scrollHeight, 50) + 'px';
        }
    }, [contentDescription]);

    // Scroll to Top Button Logic
    useEffect(() => {
        const handlePageScroll = () => {
            if (backToTopBtnRef.current) {
                if (window.scrollY > 3 * window.innerHeight) {
                    backToTopBtnRef.current.style.opacity = '1';
                    backToTopBtnRef.current.style.visibility = 'visible';
                } else {
                    backToTopBtnRef.current.style.opacity = '0';
                    backToTopBtnRef.current.style.visibility = 'hidden';
                }
            }
        };

        window.addEventListener('scroll', handlePageScroll);
        return () => window.removeEventListener('scroll', handlePageScroll);
    }, []);

    // Close settings dropdown when clicking outside
    useEffect(() => {
        const handleOutsideClick = (event) => {
            if (
                appSettingsDropdownRef.current &&
                !appSettingsDropdownRef.current.contains(event.target) &&
                event.target.id !== 'appSettingsIcon' &&
                !event.target.closest('#appSettingsIcon')
            ) {
                setIsAppSettingsDropdownVisible(false);
            }
        };
        document.addEventListener('click', handleOutsideClick);
        return () => document.removeEventListener('click', handleOutsideClick);
    }, []);

    const handleImageFileChange = (event) => {
        setImageFile(event.target.files[0]);
    };

    // ... inside the SocialMediaFeed component

 
    const handleContentUpload = async () => {
        
        if (!user || !user.id) {
            console.error('User not logged in');
            alert('Please log in to upload an item');
            return;
        }

        if (!imageFile) {
            alert('Please choose an image file to upload.');
            return;
        }
        if (!contentTitle) {
            alert('Please enter a title for your item.');
            return;
        }
        if (!contentDescription) {
            alert('Please enter a description for your item.');
            return;
        }

        const dataForm = new FormData();
        dataForm.append('image', imageFile);
        dataForm.append('title', contentTitle);
        dataForm.append('description', contentDescription);
        dataForm.append('user_id', user.id);

        try {
            const response = await axiosInstance.post('/posts', dataForm, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            });
console.log(response);
            setItems((prevItems) => [response.data.data, ...prevItems]);
            setContentTitle('');
            setContentDescription('');
            setImageFile(null);

            if (contentUploadDescriptionRef.current) {
                contentUploadDescriptionRef.current.style.height = '50px';
            }
        } catch (error) {
            console.error('Error uploading item:', error);
            alert('Failed to upload item. Please try again.');
        }
    };

    const handleLikeIconClick = (itemId) => {
        setItems((prevItems) =>
            prevItems.map((item) =>
                item.id === itemId
                    ? { ...item, liked: !item.liked, likes: item.liked ? item.likes - 1 : item.likes + 1 }
                    : item
            )
        );
        if (isItemDetailModalVisible && activeModalItem && activeModalItem.id === itemId) {
            setActiveModalItem((prev) => ({
                ...prev,
                liked: !prev.liked,
                likes: prev.liked ? prev.likes - 1 : prev.likes + 1,
            }));
        }
    };

    const displayItemDetailModal = (item) => {
        setActiveModalItem(item);
        setIsItemDetailModalVisible(true);
        document.body.style.overflow = 'hidden';
    };

    const hideItemDetailModal = () => {
        setIsItemDetailModalVisible(false);
        setActiveModalItem(null);
        document.body.style.overflow = '';
    };

    const displayUserProfileModal = () => {
        setIsUserProfileModalVisible(true);
        document.body.style.overflow = 'hidden';
    };

    const hideUserProfileModal = () => {
        setIsUserProfileModalVisible(false);
        document.body.style.overflow = '';
    };

    const displayUserLoginModal = () => {
        setIsUserLoginModalVisible(true);
        document.body.style.overflow = 'hidden';
    };

    const hideUserLoginModal = () => {
        setIsUserLoginModalVisible(false);
        document.body.style.overflow = '';
    };

    const switchAppSettingsDropdown = (event) => {
        event.stopPropagation();
        setIsAppSettingsDropdownVisible((prev) => !prev);
    };

    const handleAppSettingClick = (option) => {
        console.log(`Settings option clicked: ${option}`);
        setIsAppSettingsDropdownVisible(false);
    };

    const displayDeleteConfirmationModal = (itemId) => {
        setItemToDeleteId(itemId);
        setIsDeleteConfirmationModalVisible(true);
        document.body.style.overflow = 'hidden';
        setIsItemDetailModalVisible(false);
    };

    const dismissDelete = () => {
        setIsDeleteConfirmationModalVisible(false);
        setItemToDeleteId(null);
        document.body.style.overflow = '';
    };

    const approveDelete = () => {
        if (itemToDeleteId) {
            setItems((prevItems) => prevItems.filter((item) => item.id !== itemToDeleteId));
            console.log(`Item with ID ${itemToDeleteId} deleted.`);
            setIsDeleteConfirmationModalVisible(false);
            setItemToDeleteId(null);
            document.body.style.overflow = '';
        }
    };

    const moveToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    };

    if (loading) {
        return null; // Or return a loading spinner
    }

    return (
        <>
            <div className="social-feed-wrapper">
                <header className="main-header">
                    <div className="main-header-start">
                       <a href="/"> <img
                            src={logo}
                            alt="Brand Logo"
                            className="brand-logo"
                        /></a>
                    </div>
                    <div className="main-header-end">
                        {!authToken && (
                            <button className="signin-btn-main-header" onClick={displayUserLoginModal}>
                                Login
                            </button>
                        )}
                        <div className="main-header-settings-trigger" id="appSettingsIcon" onClick={switchAppSettingsDropdown}>
                            <img src="https://cdn-icons-png.flaticon.com/512/107/107936.png" alt="Settings" />
                        </div>
                        {isAppSettingsDropdownVisible && (
                            <div id="appSettingsMenu" className="settings-menu" ref={appSettingsDropdownRef}>
                                <ul>
                                    <li onClick={() => handleAppSettingClick('Profile')}>Profile</li>
                                    <li onClick={() => handleAppSettingClick('Saved')}>Saved</li>
                                    <li onClick={() => handleAppSettingClick('Settings')}>Settings</li>
                                    <li onClick={() => handleAppSettingClick('Switch Accounts')}>Switch Accounts</li>
                                    <li onClick={() => handleAppSettingClick('Report a problem')}>Report a problem</li>
                                    {user && <li onClick={logout}>Logout</li>}
                                </ul>
                            </div>
                        )}
                    </div>
                </header>

                {user && (
                    <div className="content-upload-area">
                        <div className="creator-info">
                            <img
                                src={user?.avatar || DefaultAvatarIcon}
                                alt="Creator Avatar"
                                className="creator-avatar"
                                id="creatorAvatar"
                                onClick={displayUserProfileModal}
                            />
                            <span className="creator-name">{user.name}</span>
                        </div>

                        <div className="content-upload-fields">
                            <input
                                type="text"
                                id="contentUploadTitle"
                                placeholder="Enter title for your content"
                                value={contentTitle}
                                onChange={(e) => setContentTitle(e.target.value)}
                            />
                            <textarea
                                id="contentUploadDescription"
                                placeholder="Write a description for your image..."
                                value={contentDescription}
                                onChange={(e) => setContentDescription(e.target.value)}
                                ref={contentUploadDescriptionRef}
                            ></textarea>
                            <input
                                type="file"
                                id="selectImageFile"
                                className="select-file-field"
                                accept="image/*"
                                onChange={handleImageFileChange}
                            />
                        </div>

                        <div className="content-upload-actions">
                            <label htmlFor="selectImageFile" className="styled-file-selector">
                                <img src={SelectImageIcon} alt="Select" /> Select
                            </label>
                            <button className="content-upload-btn" onClick={handleContentUpload}>
                                <img src={UploadImageIcon} alt="Upload" /> Upload
                            </button>
                        </div>
                    </div>
                )}

                <section className="photo-grid" ref={photoGridRef}>
                    {items?.map((item) => item && (
                        <div className="grid-item" key={item.id} data-id={item.id} onClick={() => displayItemDetailModal(item)}>
                            <div className="grid-item-image-container">
                                {item.image ? (
                                    <img src={item.image} alt={item.title} className="grid-item-image" />
                                ) : (
                                    <span className="grid-item-image-alt">No Image Available</span>
                                )}
                            </div>
                            <div className="grid-item-actions">
                                <span
                                    className="like-btn"
                                    data-liked={item.liked}
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        handleLikeIconClick(item.id);
                                    }}
                                >
                                    &#x2764;
                                </span>
                                <span className="like-counter">{item.likes} Likes</span>
                                <span className="post-date">{item.date}</span>
                                {/* <img
                                    src={DeleteBtn}
                                    alt="Delete"
                                    className="delete-btn"
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        displayDeleteConfirmationModal(item.id);
                                    }}
                                /> */}
                            </div>
                            <div className="grid-item-info">
                                <h3 className="grid-item-title">{item.title}</h3>
                                <p className="grid-item-description-preview">
                                    {item.description}
                                </p>
                                {item.description.length > readMoreLimit && (
                                    <span
                                        className="view-more-grid"
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            displayItemDetailModal(item);
                                        }}
                                    >
                                        View more
                                    </span>
                                )}
                            </div>
                        </div>
                    ))}
                    {areItemsLoading && (
                        <div className="loading-indicator">
                            <div className="spinner"></div>
                            <p>Loading more items...</p>
                        </div>
                    )}
                    {!areItemsLoading && !canLoadMore && (
                        <div className="end-of-feed">
                            <p>You've reached the end!</p>
                        </div>
                    )}
                </section>

                {isItemDetailModalVisible && activeModalItem && (
                    <div
                        id="itemDetailPopup"
                        className="popup-background"
                        ref={itemDetailModalRef}
                        onClick={(e) => e.target === itemDetailModalRef.current && hideItemDetailModal()}
                    >
                        <span className="popup-close-btn" onClick={hideItemDetailModal}>
                            &times;
                        </span>
                        <div className="popup-body">
                            <div className="popup-image-section">
                                {activeModalItem.image ? (
                                    <LazyLoadImage src={activeModalItem.image} alt="Full Item Image" className="popup-image" />
                                ) : (
                                    <span className="grid-item-image-alt">No Image Available</span>
                                )}
                            </div>
                            <div className="popup-details-section">
                                <div className="item-metadata">
                                    <span
                                        className="like-btn"
                                        data-liked={activeModalItem.liked}
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            handleLikeIconClick(activeModalItem.id);
                                        }}
                                    >
                                        &#x2764;
                                    </span>
                                    <span className="like-counter">{activeModalItem.likes} Likes</span>
                                     <span className="post-date">{activeModalItem.date}</span>
                                    {/* <img
                                        src="https://www.svgrepo.com/show/528392/delete.svg"
                                        alt="Delete"
                                        className="delete-btn"
                                        data-item-id={activeModalItem.id}
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            displayDeleteConfirmationModal(activeModalItem.id);
                                        }}
                                    /> */}
                                </div>
                                <h3 className="item-title-popup">{activeModalItem.title}</h3>
                                <p className="item-description-popup">{activeModalItem.description}</p>
                            </div>
                        </div>
                    </div>
                )}

                {isUserProfileModalVisible && (
                    <div id="userProfilePopup" className="popup-background" ref={userProfileModalRef}>
                        <span className="popup-close-btn profile-popup-close-btn" onClick={hideUserProfileModal}>
                            &times;
                        </span>
                        <div className="popup-body">
                            <div className="popup-user-profile-body">
                                <img
                                    src={user?.avatar || DefaultAvatarIcon}
                                    alt="User"
                                    className="creator-avatar"
                                />
                                <span className="creator-name">{user?.name || 'Guest'}</span>
                            </div>
                        </div>
                    </div>
                )}

                {isUserLoginModalVisible && (
                    <div
                        id="userLoginPopup"
                        className="popup-background"
                        ref={userLoginModalRef}
                        onClick={(e) => e.target === userLoginModalRef.current && hideUserLoginModal()}
                    >
                        <span className="popup-close-btn login-popup-close-btn" onClick={hideUserLoginModal}>
                            &times;
                        </span>
                        <div className="popup-body">
                            <h2>Log In</h2>
                            <button className="third-party-login-btn facebook" onClick={() => loginWith("facebook")}>
                                <img src={FacebookIcon} alt="Facebook" /> Login with Facebook
                            </button>
                            <button className="third-party-login-btn instagram" onClick={() => loginWith("instagram")}>
                                <img src={InstagramIcon} alt="Instagram" /> Login with Instagram
                            </button>
                            <button className="third-party-login-btn google" onClick={() => loginWith("google")}>
                                <img src={GoogleIcon} alt="Google" /> Login with Google
                            </button>
                        </div>
                    </div>
                )}

                {isDeleteConfirmationModalVisible && (
                    <div
                        id="deleteConfirmationPopup"
                        className="popup-background"
                        ref={deleteConfirmationModalRef}
                        onClick={(e) => e.target === deleteConfirmationModalRef.current && dismissDelete()}
                    >
                        <div className="popup-body">
                            <h2>Confirm Deletion</h2>
                            <p>Are you sure you want to delete this item? This action cannot be undone.</p>
                            <div className="action-buttons">
                                <button id="dismissDeleteBtn" onClick={dismissDelete}>
                                    Cancel
                                </button>
                                <button id="approveDeleteBtn" onClick={approveDelete}>
                                    Delete
                                </button>
                            </div>
                        </div>
                    </div>
                )}

                <button id="backToTopButton" ref={backToTopBtnRef} onClick={moveToTop}>
                    <img src={UpArrowIcon} alt="Scroll to top" />
                </button>
            </div>
        </>
    );
};

export default SocialMediaFeed;