import React from 'react';
// NOTE: I've added two new image imports (legendimg3 and legendimg4) for the new sections.
// Please ensure you have these images in your assets folder.
import legendimg1 from '../assets/kavumainpageimg/legendimg1.webp';
import legendimg2 from '../assets/kavumainpageimg/legendimg2.webp'; // Corrected to a unique image
import legendimg3 from '../assets/kavumainpageimg/legendimg3.webp';
import legendimg4 from '../assets/kavumainpageimg/legendimg4.webp';

const Legend = () => (
    <div className="page" id="page3">
        <h2 className="content-title">ഐതിഹ്യം</h2>
        <div id="legend-content">

            {/* Section 1: The Divine Princess */}
            <div className="legend-row">
                
                <div className="legend-text-wrapper">
                    <p className="legend-paragraph">
                        ആര്യ രാജാവിന്റെ മകളും തോഴിമാരും കൂടി മീനമാസത്തിലെ കാർത്തിക നാളിൽ പൂരം നോറ്റ് പൂരത്തിനു വേണ്ടുന്ന പൂക്കൾ പറിച്ചെടുക്കുവാൻ ആര്യ പൂങ്കാവിലേക്ക് പ്രവേശിക്കുകയും പൂ പറിച്ച് കൂട്ടയിലും മടിയിലും നിറച്ച് ആര്യ പൊൻ കുളത്തിൽ കുളിച്ച് കന്യകയും തോഴിമാരും കൂടി വസ്ത്രങ്ങൾ അണിഞ്ഞ് ആദിത്യ ഭഗവാനെ തൊഴുകയോടെ വന്ദിച്ച് മടിയിൽ നിന്ന് ഒരു പൂവെടുത്ത് മുടിയിൽ ചൂടുകയും ഇരു പൂവെടുത്ത് സൂര്യഭഗവാനെ ആരാധിക്കുകയും ചെയ്തു. കൊട്ടാരത്തിലേക്ക് തിരിച്ചു വരുമ്പോൾ തളർന്ന് മോഹാലസ്യപ്പെട്ടു വീണു. തോഴിമാരെല്ലാം കൂടി വാരിയെടുത്ത് രാജാവിന്റെ മുന്നിൽ എത്തിച്ചു. മകൾക്ക് എന്താണ് സംഭവിച്ചത് എന്നറിയുവാൻ ജ്യോതിഷനെ വരുത്തി പ്രശ്ന ചിന്ത നടത്തിയപ്പോൾ കന്യകയിൽ ദൈവീകത്വം ഉണ്ടെന്നും ദേവിക്ക് മലനാട് കാണുവാൻ ആഗ്രഹമുണ്ടെന്നും കണ്ടു.
                    </p>
                </div>
                <div className="legend-image-wrapper">
                    <img src={legendimg1} alt="ആര്യ രാജകുമാരിയും തോഴിമാരും" className="legend-image" />
                </div>
            </div>

            {/* Section 2: The Journey to Malanadu */}
            <div className="legend-row reverse">
                
                <div className="legend-text-wrapper">
                    <p className="legend-paragraph">
                        മലനാടിലേക്ക് യാത്ര ചെയ്യുവാൻ വേണ്ടുന്ന മരക്കലം ഒരുക്കുവാൻ വിശ്വകർമ്മാവിനെ വിളിച്ചുവരുത്തി നല്ല മുഹൂർത്തം നോക്കി ചന്ദനമരത്തിൽ ആര്യ പൂമാലയ്ക്കും ഒപ്പമുള്ളവർക്കും ഇരിപ്പിട സ്ഥാനവും നൽകി ആര്യകപ്പലിന്റെ പണി പൂർത്തിയാക്കി. പുറപ്പെടുമ്പോൾ എന്ത് തടസ്സങ്ങൾ ഉണ്ടായാലും നീക്കുന്നതിനായി പടക്കു വേണ്ടുന്ന ചേകവന്മാരെയും ഒപ്പം കൂട്ടിയിരുന്നു. ആര്യക്കര എന്ന അഴിമുഖത്ത് നിന്ന് കപ്പൽ പുറപ്പെട്ടു പൂമാരുതൻ കോട്ടയ്ക്കരികിൽ കപ്പലടിപ്പിക്കുകയും അവിടെനിന്ന് പൂമാരുതനെയുംകൂട്ടി നൂറോളം വഴിയും കടന്ന് നൂറ്റിയെട്ടാമത് രാമന്തളിയിലെ എടത്തൂർ അഴിയും കടന്ന് ഏഴുമലക്കരികിൽ കപ്പലടിപ്പിക്കുകയും ചെയ്തു. അവിടെനിന്ന് ശങ്കരനാരായണനെ വന്ദിച്ച് ആദി കഴകമായ കുറുവന്തട്ടയിൽ തങ്ങുകയും അള്ളടം ദേശത്തെ തന്റെ സാന്നിധ്യം കൊണ്ട് അനുഗ്രഹിച്ചശേഷം പയ്യന്നൂർ പെരുമാളെയും ചക്രപാണിയെയും ക്ഷേത്രപാലകനെയും വണങ്ങി കുളങ്ങാട്ട് മനയിൽ ചെന്നു ഒടുവിൽ കുട്ടമംഗലത്ത് പൂമാല സ്ഥാനം ഉറപ്പിച്ചു എന്നാണ് ചരിത്ര കഥ.
                    </p>
                </div>
                <div className="legend-image-wrapper">
                    <img src={legendimg2} alt="ദേവിയുടെ കപ്പൽ യാത്ര" className="legend-image" />
                </div>
            </div>

            {/* Section 3: The Devotee's Disappointment */}
            <div className="legend-row">
                
                <div className="legend-text-wrapper">
                    <p className="legend-paragraph">
                        ശ്രീ പൂമാല ഭഗവതിയിൽ അളവറ്റ ഭക്തി ജനിച്ച കുപ്പത്തിതറവാട്ടിലെ ഒരു സ്ത്രീയെ മുണ്ടയാട്ട് തറവാട്ടിലേക്ക് വിവാഹം ചെയ്തു കൊണ്ടുവന്നിരുന്നു. ആ സ്ത്രീ തന്റെ ഭക്തി ഭാജനമായ പൂമാല ദേവിക്ക് അടിയന്തരത്തിന് നിവേദിക്കുവാൻ പരിശുദ്ധിയോടു കൂടി തയ്യാറാക്കിയ ഉണക്കലരിയുമായി കുട്ടമത്ത് പൂമാല കാവിലേക്ക് പുറപ്പെട്ടു. രാവിലെ എത്തുമ്പോഴേക്കും കാവിലെ അടിയന്തരം കഴിഞ്ഞിരുന്നു. അവിടെ അവർ പരിഹസിക്കപ്പെട്ടു, മനസ്താപത്തോടെ തൊഴുത് ഉള്ളുരുകി പ്രാർത്ഥിച്ചു. സമയം ഏറെ വൈകിയതിനാൽ അടുത്തുള്ള ബന്ധുവീട്ടിൽ താമസിച്ചു അടുത്തദിവസം പുലർച്ചെ തിരുനടയിൽ എത്തി അമ്മയെ വിളിച്ച് പ്രാർത്ഥിച്ച് കണ്ണീരോടെ അരിയുമായി തിരികെ വീട്ടിലേക്കു മടങ്ങി.
                    </p>
                </div>
                <div className="legend-image-wrapper">
                    <img src={legendimg3} alt="കുട്ടമത്ത് കാവിലെ ഭക്ത" className="legend-image" />
                </div>
            </div>

            {/* Section 4: The Founding of the New Kavu */}
            <div className="legend-row reverse">
                
                <div className="legend-text-wrapper">
                    <p className="legend-paragraph">
                        തന്റെ വീട്ടിൽ തിരിച്ചെത്തിയ സ്ത്രീ താൻ തലേദിവസം കൊണ്ടു പോയി മടക്കി കൊണ്ടുവന്ന ഉണക്കലരി കൊട്ടിലിൽ വച്ചപ്പോൾ അതിൽ ചില ദൃഷ്ടാന്തങ്ങൾ കാണാനിടയായി. ഭയാശങ്കയോടെ നടന്ന കാര്യങ്ങൾ നാട്ടിലെ മന്നനായ നമ്പിയെ ധരിപ്പിക്കുകയും ചെയ്തു. അതു കേട്ട നമ്പി പ്രശ്ന ചിന്ത നടത്തി, സ്ത്രീയോടൊപ്പം പൂമാലിക എഴുന്നെള്ളിയതായി കാണാൻ സാധിച്ചു. ദേവി പ്രതിഷ്ഠക്കായി ആരൂഡം നിർമ്മിക്കണമെന്നും കണ്ടു. പത്തുമാസം കഴിഞ്ഞിട്ടും പ്രസവിക്കാതെ നിൽക്കുന്ന തന്റെ മരുമകൾക്ക് സുഖപ്രസവം സിദ്ധിച്ചാൽ ദേവിക്ക് ഇരിപ്പടമായി ആരൂഡ സ്ഥാനം പണിയുമെന്ന് അറിയിച്ചു. ആഗ്രഹം സാധിച്ചു കിട്ടിയ സന്തോഷത്തിൽ തരണനെല്ലൂർ നമ്പൂതിരിപ്പാടിനെ വരുത്തി ഭൂമി ശുദ്ധി വരുത്തി തന്റെ ഭവനത്തിന്റെ കന്നിയിൽ പൂമാലികയുടെ ആരൂഡത്തിന് കുറ്റിയടിച്ച്, തന്റെ ഭവനവും സ്ഥലവും ദേവിക്ക് സമർപ്പിച്ചു. തലേന്നാൾ തിരിച്ചുകൊണ്ടുവന്ന ഉണക്കലരി നിവേദ്യമായി സമർപ്പിച്ചു. തലേന്നാളത്തെ അരി ഇഷ്ടനിവേദ്യമാക്കിയ ദൈവ സന്നിധി ശ്രീ തലയന്നേരി പൂമാല ഭഗവതി കാവായി.
                    </p>
                </div>
                <div className="legend-image-wrapper">
                    <img src={legendimg4} alt="തലയന്നേരി കാവിന്റെ ഉത്ഭവം" className="legend-image" />
                </div>
            </div>
            
        </div>
    </div>
);

export default Legend;