import React from 'react';
import logo from '../assets/kavumainpageimg/logo-kavu.png';

function PrivacyPolicy() {
  // --- STYLES ---
  // All styles are defined in this object for easy management.
  const styles = {
    container: {
      maxWidth: '800px',
      margin: '2rem auto',
      padding: '2rem',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
      backgroundColor: '#ffffff',
      lineHeight: '1.7',
      color: '#333',
      borderRadius: '8px',
      boxShadow: '0 2px 10px rgba(0,0,0,0.05)',
    },
    title: {
      fontSize: '2.2rem',
      color: '#2c3e50',
      borderBottom: '2px solid #ecf0f1',
      paddingBottom: '0.5rem',
      marginBottom: '0.5rem',
    },
    lastUpdated: {
      fontSize: '0.9rem',
      fontStyle: 'italic',
      color: '#95a5a6',
      marginBottom: '2rem',
    },
    heading: {
      fontSize: '1.6rem',
      color: '#34495e',
      marginTop: '2.5rem',
      marginBottom: '1rem',
    },
    paragraph: {
      fontSize: '1rem',
      color: '#555',
      marginBottom: '1rem',
    },
    list: {
      paddingLeft: '20px',
    },
    listItem: {
      marginBottom: '0.5rem',
    },
    link: {
      color: '#d9534f', // A thematically consistent link color
      textDecoration: 'none',
    },
    strong: {
      fontWeight: 'bold',
      color: '#333',
    },
       navContainer: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      padding: '0.5rem 2rem', // Reduced padding for a shorter navbar
      backgroundColor: '#ffffff',
      boxShadow: '0 2px 8px rgba(114, 59, 59, 0.1)',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
    },
    logoLink: {
      display: 'flex', // Helps with vertical alignment
      alignItems: 'center',
    },
    // NEW STYLE: Controls the size of your logo image.
    logoImage: {
      height: '50px', // <-- ADJUST THIS VALUE TO MAKE LOGO BIGGER OR SMALLER
      width: 'auto', // Automatically adjusts width to maintain aspect ratio
    },
    code: {
        fontFamily: 'monospace',
        backgroundColor: '#ecf0f1',
        padding: '2px 6px',
        borderRadius: '4px',
    }
  };
  // --- END OF STYLES ---


  // IMPORTANT: Fill in your temple's specific details below
  const templeDetails = {
    name: "Sree Thalayanneri poomala bhagavathi kavu",
    websiteUrl: "www.sreethalayanneripoomalakavu.in",
    address: "annur,payyanur,kannur ,dist kannur,state kerala, pin 670307",
    email: "sreethalayanneripoomala@gmail.com",
    phone: "+91 7909230201",
  };


  return (
    <div style={styles.container}>
         <nav style={styles.navContainer}>
      {/* The logo is now an image inside the link */}
      <a href="/" style={styles.logoLink}>
        <img 
          src={logo} 
          alt="Sree Thalayanneri poomala bhagavathi kavu Logo" 
          style={styles.logoImage} 
        />
      </a>

      {/* Other navigation links can be added here */}
    </nav>
      <h1 style={styles.title}>Privacy Policy for {templeDetails.name}</h1>
      <p style={styles.lastUpdated}>Last Updated: July 4, 2025</p>

      <h2 style={styles.heading}>1. Introduction</h2>
      <p style={styles.paragraph}>
        Welcome to the official website for <strong style={styles.strong}>{templeDetails.name}</strong>. We are deeply committed to protecting the privacy of our devotees and visitors. This Privacy Policy explains how we collect, use, and protect your information when you visit our website (<code style={styles.code}>{templeDetails.websiteUrl}</code>) and choose to log in using third-party services.
      </p>

      <h2 style={styles.heading}>2. Information We Collect</h2>
      <h3 style={{...styles.heading, fontSize: '1.2rem'}}>a) Information from Social Media Logins</h3>
      <p style={styles.paragraph}>
        Our website allows you to log in using your existing accounts from services like Google, Facebook, or Instagram. When you do, you grant us permission to access certain profile information, typically including your <strong style={styles.strong}>Name, Email Address,</strong> and <strong style={styles.strong}>Profile Picture</strong>.
      </p>
      <p style={styles.paragraph}>
        <strong style={styles.strong}>Important:</strong> We do not receive or store the password for your social media accounts. Your login is handled securely by the respective platform.
      </p>
      <h3 style={{...styles.heading, fontSize: '1.2rem'}}>b) Automatically Collected Data</h3>
       <p style={styles.paragraph}>
        Like most websites, we automatically collect basic technical data such as your IP address and browser type to help us improve our site. This data is anonymous.
      </p>

      <h2 style={styles.heading}>3. How We Use Your Information</h2>
      <p style={styles.paragraph}>
        We use your information solely to:
      </p>
      <ul style={styles.list}>
        <li style={styles.listItem}>Create and manage your logged-in session.</li>
        <li style={styles.listItem}>Personalize your experience (e.g., greet you by name).</li>
        <li style={styles.listItem}>Analyze usage to improve our website's content and performance.</li>
      </ul>

      <h2 style={styles.heading}>4. Data Sharing and Disclosure</h2>
      <p style={styles.paragraph}>
        We <strong style={styles.strong}>do not sell, trade, or rent</strong> your personal information. Your data is only shared with service providers for essential functions like website hosting, or if required by law.
      </p>

      <h2 style={styles.heading}>5. Third-Party Services</h2>
      <p style={styles.paragraph}>
        Your use of social logins is also subject to the privacy policies of those platforms. We encourage you to review them.
      </p>

      <h2 style={styles.heading}>6. Your Data Rights</h2>
      <p style={styles.paragraph}>
        You can revoke our access to your social account at any time via your social media settings. To have your stored profile information deleted from our website, please contact us.
      </p>

      <h2 style={styles.heading}>7. Contact Us</h2>
      <p style={styles.paragraph}>
        If you have any questions, please contact us:
      </p>
      <ul style={{...styles.list, listStyle: 'none'}}>
        <li><strong style={styles.strong}>Address:</strong> {templeDetails.address}</li>
        <li><strong style={styles.strong}>Email:</strong> {templeDetails.email}</li>
        <li><strong style={styles.strong}>Phone:</strong> {templeDetails.phone}</li>
      </ul>
    </div>
  );
}

export default PrivacyPolicy;